<script lang="ts">
	import Datepicker from 'svelte-calendar';

	import electric from './data/electric/data.json';
	import gas from './data/gas/data.json';
	
	$: startDate = new Date(2021, 0, 30, 23, 30, 0);
	$: endDate = new Date(2021, 0, 31);
	let userHasChosenDate1: boolean;
	let userHasChosenDate2: boolean;
	
	let el;

	$: data = electric.results
		.filter(e =>{
			const startD = new Date(e.interval_start);
			const endD = new Date(e.interval_end);
			return startD >= startDate && endD <= endDate;
		})
		.sort((a, b) => (new Date(a.interval_start) > new Date(b.interval_start)) ? 1  : -1 )

		.map(e => {
			let gas_item = gas.results.find(g => g.interval_end == e.interval_end && g.interval_start == e.interval_start);
			return {
				electric_consumption: e.consumption,
				gas_consumption: gas_item?.consumption ?? 0,
				interval_start: e.interval_start,
				interval_end: e.interval_end
			}
		} )
</script>

<main>
	Start date <Datepicker bind:selected={startDate} bind:dateChosen={userHasChosenDate1}/>	
	End date <Datepicker bind:start={startDate} bind:selected={endDate} bind:dateChosen={userHasChosenDate2}/>
	{#each data as d}
	<div class="chart">
		<div style="color: black;text-align: left;">{d.interval_start}</div>
		<div style="background-color: steelblue; width: {d.electric_consumption * 1000}px">{d.electric_consumption} kWh</div>

		<div style="background-color: red; width: {d.gas_consumption * 250}px">{d.gas_consumption} m3</div>
	</div>
	{/each}
</main>

<style>
	main {
		text-align: center;
		padding: 1em;
		max-width: 240px;
		margin: 0 auto;
	}

	.chart :global(div) {
		font: 10px sans-serif;
		text-align: right;
		padding: 3px;
		margin: 1px;
		color: white;
	}

	@media (min-width: 640px) {
		main {
			max-width: none;
		}
	}
</style>